<template>
	<div id="spread-index">
		<public-header title="推广员"></public-header>
		<div class="content">
			<div class="headerinfo">
				<div class="userinfo">
					<div class="fui-list">
						<div class="fui-img">
							<img :src="member.avatar" alt="">
						</div>
						<div class="fui-info">
							<ul>
								<li>{{member.mobile}}</li>
								<li class="fui-color">
									{{member.groupname ? '<' + member.groupname + '>': '您还没有等级'}}
									<span class="open-popup" @click="agreementShow = true">升级攻略</span>
								</li>
								<li>邀请码:{{member.uid}}</li>
							</ul>
						</div>
					</div>
				</div>
				<div class="userblock">
					<div class="block-top">
						<div class="top-title">
							成功提现佣金
						</div>
						<div class="top-num">
							{{spread.commission_getcash_success}}
						</div>
					</div>
					<div class="block-bottom">
						<router-link :to="util.getUrl({path: '/pages/spread/getCash/application'})" class="bottom-right" v-if="member.spreadcredit2 > 0">
							佣金提现
						</router-link>
						<div class="bottom-title">
							可提现佣金
						</div>
						<div class="bottom-num">
							{{member.spreadcredit2}}
						</div>
					</div>
				</div>
			</div>
			<div class="content-sudoku">
				<div class="wui-grids">
					<router-link :to="util.getUrl({path: '/pages/spread/commission'})" class="wui-grid">
						<van-icon name='refund' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		推广佣金
					 	</div>
					 	<div class="sudoku-num">
							<span>{{spread.commission_grandtotal}}</span>元
					 	</div>
					</router-link>
					<router-link :to="util.getUrl({path: '/pages/spread/current'})" class="wui-grid">
						<van-icon name='list' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		佣金明细
					 	</div>
					 	<div class="sudoku-num">
							<span>{{current}}</span>笔
					 	</div>
					</router-link>
					<router-link :to="util.getUrl({path: '/pages/spread/getCash/index'})" class="wui-grid">
						<van-icon name='sort' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		提现明细
					 	</div>
					 	<div class="sudoku-num">
							<span>{{commission}}</span>笔
					 	</div>
					</router-link>
					<router-link :to="util.getUrl({path: '/pages/spread/order'})" class="wui-grid">
						<van-icon name='order' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		推广订单
					 	</div>
					 	<div class="sudoku-num">
							<span>{{order}}</span>笔
					 	</div>
					</router-link>
					<router-link :to="util.getUrl({path: '/pages/spread/down'})" class="wui-grid">
						<van-icon name='friend' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		我的团队
					 	</div>
					 	<div class="sudoku-num">
							<span>{{down}}</span>人
					 	</div>
					</router-link>
					<router-link :to="util.getUrl({path: '/pages/spread/poster'})" class="wui-grid">
						<van-icon name='fukuanma' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		推广二维码
					 	</div>
					 	<div class="sudoku-num">
							
					 	</div>
					</router-link>
					<router-link :to="util.getUrl({path: '/pages/spread/rank'})" class="wui-grid">
						<van-icon name='hot1' class="sudoku-img"></van-icon>
					 	<div class="sudoku-info">
					 		佣金排名
					 	</div>
					 	<div class="sudoku-num">
							
					 	</div>
					</router-link>
				</div>
			</div>
		</div>
		<van-popup v-model="agreementShow" position="bottom" class="agreement-popup">
			<van-nav-bar class="border-0px" title="规则详解" @click-left="agreementShow = false" :style="{background: '#ff2d4b', color: '#fff' }">
				<van-icon name="left" :style="{color: '#fff'}" slot="left" class="font-20"></van-icon>
			</van-nav-bar>
			<div class="popup-content margin-10" v-html="upgrade_explain"></div>
		</van-popup>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader,
		},
  		data() {
  			return {
  				agreementShow: false,
  				member: {},
  				spread: {},
  				order: '',
  				down: '',
  				current: '',
  				commission: '',
  				upgrade_explain: ''
  			}
  		},
  		methods: {
			onLoad() {
				this.util.request({
					url: 'spread/index',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno == -1000) {
						this.$router.push(this.util.getUrl({path: '/pages/spread/register'}));
						return;
					} 
					result = result.message;
					this.member = result.member;
	  				this.commission = result.commission;
	  				this.order = result.order;
	  				this.spread = result.spread;
	  				this.down = result.down;
	  				this.current = result.current;
	  				this.upgrade_explain = result.upgrade_explain;
				})
			},
		},
		mounted() {
			this.onLoad()
		}
	}
</script>

<style>
	#spread-index .content{
		background: #fff;
	}
    #spread-index .content .headerinfo{
	    height: auto;
	    background: #FE5455;
	    border-bottom: 1px #fa9d9d solid;
    }
    #spread-index .content .headerinfo .userinfo{
	    min-height: 80px;
	    padding: 6px 12px;
	    border-bottom: 1px solid #fe9292;
	    position: relative;
    }
    #spread-index .content .headerinfo .userinfo .fui-list{
		padding: 4px 0 0 0;
	    overflow: hidden;
    }
    #spread-index .content .headerinfo .userinfo .fui-list .fui-img{	
	    width: 50px;
	    height: 50px;
	    margin-right: 10px;
	    color: #aaa;
	    float: left;
	    margin-top: 15px;
    }
    #spread-index .content .headerinfo .userinfo .fui-list .fui-img img{
		height: 46px;
	    width: 46px;
	    border-radius: 46px;
	    border: 2px solid #fff;
	    display: block;
	    overflow: hidden;
    }
    #spread-index .content .headerinfo .userinfo .fui-list .fui-info{
		font-size: 13px;
	    color: #fff;
	    float: left;
	    padding-top: 11px;
    }
    #spread-index .content .headerinfo .userinfo .fui-list .fui-info ul li{
    	line-height: 19px;
    }
    #spread-index .content .headerinfo .userinfo .fui-list .fui-info .fui-color{
	    color: #fef31f;
    }
    #spread-index .content .headerinfo .userblock{
		height: auto;
	    padding: 0 12px;
	    color: #fff;
	    overflow: hidden;
    }
    #spread-index .content .headerinfo .userblock .block-top{
	    padding: 14px 0 0;
	    height: auto;
	    color: #fff;
    }
    #spread-index .content .headerinfo .userblock .block-top .top-title{
    	font-size: 14px;
    	line-height: 21px;
    }
    #spread-index .content .headerinfo .userblock .block-top .top-num{
	    font-size: 28px;
	    line-height: 34px;
	    font-weight: 100;
    }
    #spread-index .content .headerinfo .userblock .block-bottom{
	    padding-bottom: 16px;
	    padding-top: 14px;
	    overflow: hidden;
    }
    #spread-index .content .headerinfo .userblock .block-bottom .bottom-right{
		float: right;
		width: auto;
		height: 24px;
		padding: 0 8px;
		border: 1px solid #ccc;
		line-height: 24px;
		border-radius: 12px;
		font-size: 14px;
		color: #ccc;
		margin-top: 7px;
    }
    #spread-index .content .headerinfo .userblock .block-bottom .bottom-title{
	    font-size: 12px;
	    line-height: 12px;
    }
    #spread-index .content .headerinfo .userblock .block-bottom .bottom-num{
	    font-size: 20px;
	    font-weight: 100;
	    line-height: 28px;
    }
    #spread-index .content .content-sudoku{
	    background: #fff;
	    /*border-bottom: 1px solid #ececec;*/
    }
    #spread-index .content .content-sudoku .wui-grids{
    	position: relative;
    	overflow: hidden;
    }
    #spread-index .content .content-sudoku .wui-grids .wui-grid{
		position: relative;
		float: left;
		padding: 20px 10px;
		width: 33.33333333%;
		box-sizing: border-box;
    }
   	#spread-index .content .content-sudoku .wui-grids .wui-grid:before{
	    content: " ";
	    position: absolute;
	    right: 0;
	    bottom: 0;
	    color: #d9d9d9;
	    top: 0;
	    width: 1px;
	    border-right: 1px solid #d9d9d9;
   	}
   	#spread-index .content .content-sudoku .wui-grids .wui-grid:after{
		content: " ";
		position: absolute;
		right: 0;
		bottom: 0;
		color: #d9d9d9;
		left: 0;
		height: 1px;
		border-bottom: 1px solid #d9d9d9;
   	}
   	#spread-index .content .content-sudoku .wui-grids .wui-grid .sudoku-img{
   		display: inline-block;
	    height: 34px;
	    width: 100%;
	    padding-top: 20px;
	    text-align: center;
	    font-size: 28px;
	    line-height: 30px;
	    color: #0894ec;
   	}
   	#spread-index .content .content-sudoku .wui-grids .wui-grid .sudoku-info{
	    height: 20px;
	    font-size: 12px;
	    line-height: 30px;
	    text-align: center;
	    color: #666;
   	}
   	#spread-index .content .content-sudoku .wui-grids .wui-grid .sudoku-num{
		height: 24px;
	    font-size: 10px;
	    text-align: center;
	    color: #666;
	    line-height: 20px;
   	}
   	#spread-index .content .content-sudoku .wui-grids .wui-grid .sudoku-num span{
	    color: #feb312;
   	}
   	#spread-index .agreement-popup{
		height: 100%;
		width: 100%;
	}
</style>


